/* eslint-disable */ //warning 
import { lazy, Suspense, useEffect, useState } from 'react';
import { Navbar, Container, Nav, Row, Col } from 'react-bootstrap';
import { Routes, Route, Link, useNavigate, Outlet, useParams } from 'react-router-dom';
import './App.css';

/* Ajax  */
import axios from 'axios';
import { useQuery } from '@tanstack/react-query';

/*    */
import data from './data.js';

/*  js */
// import Detail from './routes/Detail.js';
// import Cart from './routes/Cart.js';

/* lazy : App.js      js  import  
        :     import 
        :  js   */
const Detail = lazy(() => import('./routes/Detail.js'));
const Cart = lazy(() => import("./routes/Cart.js"));
const Memo = lazy(() => import("./routes/Memo.js"));



function App() {

  let [shoes, setShoes] = useState(data) //
  let [show, setShow] = useState(false) // 
  let [more, setMore] = useState(true) // 
  let [pushcnt, setPushcnt] = useState(2) // 
  let [stock, setStock] = useState([10,11,12]) //

  let navigate = useNavigate()
  
  /* Nav  user  */
  let result = useQuery([''], ()=>{
    return axios.get('https://codingapple1.github.io/userdata.json')
    .then((axios_data)=>{ return axios_data.data })
    staleTime : 2000 //refetch(  )   (2 )
  })

  /*    */
  //Detail  id  localStorage  (Detail.js )
  useEffect(()=>{
    // watched localStorage   setItem  (  )
    localStorage.setItem('watched', JSON.stringify([]))
    if (localStorage.getItem('watched').length == 0){
      localStorage.setItem('watched', JSON.stringify([]))
    }
  },[])
  
  useEffect(()=>{
    //         (   )
    {
      more == false
      ? alert("   !")
      : null
    }
  }, [pushcnt])
  
  return (
    <div className="App">

      {/* Navbar */}
      <Navbar bg="light" variant="light">
        <Container>
          <Navbar.Brand onClick={()=>{ navigate('Noinara/') }} >NOI _ NARA</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{ navigate('Noinara/') }} >Home</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('Noinara/detail/0') }} >Detail</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('Noinara/cart') }} >Cart</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('Noinara/memo') }} >Memo</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('Noinara/about') }} >About</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('Noinara/event') }} >Event</Nav.Link>
          </Nav>
          <Nav className="ms-auto">
            {/* { result.isLoading ? '' : ', ' + result.data.name } */}

            {/*    ,       */}
            { result.isLoading && '' }
            { result.error && '' }
            { result.data && ', ' + result.data.name }
          </Nav>

        </Container>
      </Navbar>

      {/* Router    */}
      {/* suspense : lazy js     */}
      {/* fallback : lazy js        */}
      {/*  Routes  lazy   Suspense     */}
      <Suspense fallback={ <div className="alert alert-warning">......</div> }>
        <Routes>
          {/*  */}
          <Route path="Noinara/" element={
            <div>
              {/* MainImg */}
              <div className="main-bg"></div> 

              {/* Product */}
              <Container className="product_container" >
                <Row md={3}>
                  {/* shoes  map      */}
                  {
                    shoes.map((a,i)=>{
                      return(
                        <Card shoes={shoes} i={i} key={i} />
                      )
                    })
                  }
                </Row>
              </Container>
              
              {/* UI */}
              {
                show == true
                ? <div className="alert alert-warning">...... </div>
                : null
              }


              {/*     (Ajax) /     */}
              {
                more == true
                ? //   
                <button onClick={()=>{
                  setShow(true); // true
                  setPushcnt(pushcnt+1); 
                  console.log(pushcnt)
                  // state  ?(     2  )

                  if (pushcnt <= 3){
                    axios.get('https://codingapple1.github.io/shop/data'+ pushcnt +'.json')
                    .then((result)=>{
                      let copy = [...shoes, ...result.data]
                      setShoes(copy)
                      setShow(false); // false
                    })
                    .catch(()=>{
                      console.log(' ')
                      setShow(false); // false
                    })
                  }

                  else {
                    setShow(false); // false
                    setMore(false); // false
                  }
                }} ></button>

                :  //  
                <div className="alert alert-warning"><p>...  !...</p></div>
              }

            </div>
          } ></Route>
          {/*  / url  */}
          <Route path='Noinara/detail/:id' element={ <Detail shoes={shoes} /> } />

          {/*  */}
          <Route path="Noinara/cart" element={ <Cart shoes={shoes} /> } />

          {/* memo, useMemo */}
          <Route path="Noinara/memo" element={ <Memo /> } />

          {/*  / nested routes  */}
          <Route path='Noinara/about' element={<About/>} >
            <Route path='member' element={<div></div>} /> {/* /about/member */}
            <Route path='location' element={<div></div>} /> {/* /about/location */}
          </Route>

          {/*  */}
          <Route path='Noinara/event' element={<Event/>} >
            <Route path="one" element={<p>   </p>} />
            <Route path="two" element={<p> </p>} />
          </Route>

          {/*  */}
          <Route path='*' element={<div></div>} />
        </Routes>
      </Suspense>
    </div>
  );
}

function Event(){
  return(
    <div>
      <h4> </h4>
      <Outlet />
    </div>
  )
}

function About(){
  return(
    <div>
      <h4></h4>
      <Outlet />
    </div>
  )
}

function Card(props){
  let navigate = useNavigate()
  return (
    <Col sm >
      <div onClick={()=>{ navigate('detail/'+props.i) }} >
        <img src={"https://codingapple1.github.io/shop/shoes"+ (props.i+1) +".jpg"} width="80%" />
        <h4>{props.shoes[props.i].title}</h4>
        <p>{props.shoes[props.i].price}</p>
      </div>
    </Col>
  )
}

export default App;
